<template>
  <div class="about-movie">
    <div class="container">
      <div class="content">
        <div class="img">
          <img :src="movieInfo.Poster" />
        </div>
        <div class="title my-3">
          <h3>{{ movieInfo.Title }}</h3>
          <span>Released at {{ movieInfo.Released }}</span>
          <span class="d-block">Type: {{ movieInfo.Type }}</span>
          <span class="d-block">Country: {{ movieInfo.Country }}</span>
        </div>
        <div class="story my-3">
          <p>{{ movieInfo.Plot }}</p>
        </div>
        <div class="row">
          <div class="col-md-6">
            <div class="actors my-4">
              <h5>Actors</h5>
              <p>{{ movieInfo.Actors }}</p>
            </div>
            <div class="director">
              <h5>Directed by</h5>
              <p>{{ movieInfo.Director }}</p>
            </div>
          </div>
          <div class="col-md-6 text-md-end">
            <div class="boxoffice my-4">
              <h5>Box office</h5>
              <p>{{ movieInfo.BoxOffice }}</p>
            </div>
            <div class="rate">
              <h5>IMDB rating</h5>
              <p>{{ movieInfo.imdbRating }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "movie-info",
  data() {
    return {
      apiKey: "2c3b38a0",
      movieInfo: [],
    };
  },
  async mounted() {
    let movieData = JSON.parse(localStorage.getItem("movie-data"));
    let api = this.apiKey;
    await fetch(
      `https://www.omdbapi.com/?t=${movieData.Title}&plot=full&apikey=${api}`
    )
      .then((data) => {
        return data.json();
      })
      .then((result) => {
        this.movieInfo = result;
      });
  },
};
</script>

